#demo-container {
    max-width: 350px;
    /* center the container in middle on horizontal axis */
    /* margin: 0 auto; */

    /* add empty space above the container (20% of the view height) */
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    float: left;
    
    padding: 10px;
    margin: 10px;
    margin-left: 95px;
    border-right: 1px solid;
    background-color: #fcfcfc;
    box-shadow: 5px 5px #888888;
    margin-bottom: 50px;
}

.demo-preview{
    border: 1px solid #bacdd8;
    /* add space between the border and the content */
    padding: 28px;
    border-radius: 12px;
    transition: transform 0.2s linear;

}

.demo-preview{
    transform: translateY(-7px);
}

.tag {
    padding: 4px 8px;
    border: 1px solid #e5e5e5;

    border-radius: 50px;
    font-size: 12px;
    font-weight: 600;
    color: #788697;
}

.name {
    font-size: 24px;
    font-weight: 600;
    margin-top: 16px;

}

.demo button{
    border: none;
    /* color: #e5e5e5; */
    margin: 10px;
    color: black;
}

@media screen and (max-width: 400px) {
    #demo-container {
        max-width: 350px;
        /* center the container in middle on horizontal axis */
        /* margin: 0 auto; */
    
        /* add empty space above the container (20% of the view height) */
        margin-top: 5vh;
        display: flex;
        flex-direction: column;
        float: left;
        
        padding: 10px;
        margin: 10px;
        margin-left: 25px;
        border-right: 1px solid;
        background-color: #fcfcfc;
        box-shadow: 5px 5px #888888;
        margin-bottom: 50px;
    }
}